import React from 'react'
import styled from "styled-components"
import BackgroundImage from 'gatsby-background-image'

import {
    Circle,
    HoverContainer,
    CardWrapper,
    Accent,
    Paragraph,

} from '../elements'

const AboutCard = ({description, github, image }) => (
    <CardWrapper borderRadius="6px" width="870px" height="500px">

   
        <BackgroundImage fluid={image} >
            <HoverContainer height="600px" width="870px">
                <Paragraph>
                    {description}
                </Paragraph>
            </HoverContainer>

        </BackgroundImage>

        <CardWrapper borderRadius="6px" width="870px" height="500px">
        </CardWrapper>

    </CardWrapper>
    

    

)



export default AboutCard